import React, { Component } from 'react';
import './Player.less';
import {Slider, Icon, Modal, Toast } from 'antd-mobile';
import {InputWin} from './InputWin';
import util from '../common/ts/util';
import { AudioObject } from '../store/store';
import {observer} from 'mobx-react';

export interface IProps { 
    store: any,
    item: AudioObject,
    onRemove: (id: string)=>void,
    onPlay: (id: string)=>void
}
export interface IState{
    currentTime: number;
    duration: number,
    muted: boolean;
    paused: boolean;
    volume: number,
    totalTimeStr: string,
    currentTimeStr: string,
    active: boolean
}
@observer
class Player extends Component<IProps, IState> {
    readonly state: IState = {
        currentTime: 0,
        duration: 0,
        muted: false,
        paused: true,
        volume: 0,
        totalTimeStr: '00:00',
        currentTimeStr: '00:00',
        active: false
    };    
    refs: any = React.createRef() 
    componentDidMount() {
        // console.log(this.refs.au)
        this.refs.au.addEventListener("timeupdate", this.timeFun, false);
        this.refs.au.addEventListener("loadedmetadata", this.loadFun, false);
        this.refs.au.addEventListener("ended", this.endFun, false);
        this.refs.au.addEventListener("error", this.errorFun, false);
    }
    componentWillUnmount() {
        this.refs.au.pause();
        this.refs.au.removeEventListener("timeupdate", this.timeFun, false);
        this.refs.au.removeEventListener("loadedmetadata", this.loadFun, false);
        this.refs.au.removeEventListener("ended", this.endFun, false);
        this.refs.au.removeEventListener("error", this.errorFun, false);
        //this.releaseUrl();
    }
    releaseUrl = ()=> {
        if(this.refs.au.src.startsWith("blob:")) {
            window.URL.revokeObjectURL(this.refs.au.src);
        }
    }
    errorFun = (e:any)=> {
        Modal.alert('提示', '播放失败');
    }
    endFun = (e:any)=> {
        this.setState({
            paused: true
        })
    }
    timeFun = (e:any)=> {
        // console.log(e.target.currentTime)
        // console.log('update')
        let t = Math.ceil(e.target.currentTime);
        this.setState({
            currentTime: t,
            currentTimeStr: this.getCurrentTimeStr(t)
        })
    }
    // todo event 的类型
    loadFun = (e:any)=> {
        // console.log('load')
        let t = Math.ceil(e.target.duration);
        this.setState({
            duration: t,
            totalTimeStr: this.getTotalTimeStr(t)
        })
    }
    setTimeString = (s:number)=> {
        this.setState({
            currentTime: s
        })
    }
    resetTime = ()=> {
        this.setState({
            currentTime: 0
        })
    }
   
    setDisabled = ()=> {
        this.setState({
            muted: !this.state.muted
        })
    }

    onBtnPlayClick = () => {
        this.refs.au.play();
        this.setState({
            paused: false,
            active: true
        });
        this.props.onPlay(this.props.item.id);
    }

    onBtnPauseClick = () => {
        this.refs.au.pause();
        this.setState({
            paused: true,
            active: false
        })
    }
    onTimeSliderChange = (v:any)=> {
        console.log(v);
        this.refs.au.currentTime = v;
    }
    onEditText = () => {
        InputWin(this.props.item.text).then((text:any)=>{
            let o:AudioObject = {
                ...this.props.item
            }
            o.text = text;
            this.props.store.editItem(o);
        })
    }
    getCurrentTimeStr = (currentTime:number)=> {
        return util.second2Time(currentTime * 1000, 2, true).join(":");
    }
    getTotalTimeStr = (duration:number)=> {
        return util.second2Time(duration * 1000, 2, true).join(":");
    }
    onVolumeSliderChange = (v:any) => {
        this.refs.au.volume = v;
    }
    onAudioNameClick = ()=> {
        Modal.prompt('请输入名称', '',
        [
          {
            text: '取消',
            onPress: value => new Promise((resolve) => {
              resolve();
            }),
          },
          {
            text: '确定',
            onPress: value => new Promise((resolve, reject) => {
                value = value.trim();
                if(value == "") {
                    Toast.info("请输入名称");
                    reject();
                }
                let o = {...this.props.item};
                o.name = value;
                this.props.store.editItem(o);
                resolve();
            }),
          },
        ], 'default', this.props.item.name, ['请输入名称'])        
    }
    render() {

      return (
        <div className={this.state.active ? "c-item-box c-item-active" : "c-item-box"}>
            <audio src={this.props.item.url} ref="au"></audio>
            <div className='c-item-head'>
                {this.state.paused ? (<i className="c-item-playbtn" onClick={this.onBtnPlayClick}></i>) :
                (<i className="c-item-pausebtn" onClick={this.onBtnPauseClick}></i>)}
                <span className='c-item-audioname' onClick={this.onAudioNameClick}>{this.props.item.name||"------"}</span>
                <span className={`icon iconfont icon-yinliang1 ${this.state.muted?'disabled': null}`} onClick={this.setDisabled}></span>
                <div className='c-item-volume'>
                    <Slider
                        onChange={this.onVolumeSliderChange}
                        defaultValue={1}
                        step={0.1}
                        min={0}
                        max={1}
                        handleStyle={{
                            borderColor: '#ffffff',
                            height: '18px',
                            width: '18px',
                            marginLeft: '-7px',
                            marginTop: '-8px',
                            backgroundColor: '#ffffff',
                        }}
                        trackStyle={{
                            backgroundColor: "#cdcdcd"
                        }}                        
                    />                        
                </div>
            </div>
            <div className="c-item-body">
                <div className='c-item-time'>{this.state.currentTimeStr}/{this.state.totalTimeStr}</div>
                <div className='c-item-progressbox'>
                    <Slider
                            onChange={this.onTimeSliderChange}
                            defaultValue={0}
                            value={this.state.currentTime}
                            min={0}
                            max={this.state.duration}
                            handleStyle={{
                                borderColor: '#ffffff',
                                backgroundColor: '#ffffff',
                            }}
                            trackStyle={{
                                backgroundColor: "#cdcdcd"
                            }}
                    />  
                </div>
                <span className='icon iconfont icon-bianji c-item-text' onClick={this.onEditText}></span>             
            </div>
            <div className="c-item-foot" style={{display: this.props.item.text === "" ? "none" : "block"}}>
                 {this.props.item.text}
            </div>
            <Icon className='c-item-closebtn' type="cross-circle-o" size={'md'}  onClick={(e)=>{this.props.onRemove(this.props.item.id)}}/>
        </div>
      );
    }    
  }
  
  export default Player;
