import React, { Component, ReactNode } from 'react';
import Dialog from './Dialog';
import './RecordWin.less';
import {Modal} from 'antd-mobile';
import util from '../common/ts/util';

// 录音
const Recorder = require('recorder-core/recorder.mp3.min');
require('recorder-core/src/extensions/waveview');
export interface IProps { 
    onRef(o:any):void;
}
export interface IState{
    dialogShow: boolean;
    recording: boolean;
    time: string;
    canRecord: boolean
}

class RecordWin extends Component<IProps, IState> {
    private rec:any = null;
    private wave:any = null;
    private rs: any = null;
    private started: boolean = false;
    //记录一下录了多少秒了
    private recordTime:number = 0;

    constructor(props:any){
        super(props);
        this.props.onRef(this)
    }
    static defaultProps: IProps = {
        onRef(o:any){}
    }
    readonly state: IState = {
        dialogShow: false,
        recording: false,
        time: "00:00",
        canRecord: false
    };
    recOpen=()=>{//一般在显示出录音按钮或相关的录音界面时进行此方法调用，后面用户点击开始录音时就能畅通无阻了
        this.rec=Recorder({
            type:"mp3",sampleRate:16000,bitRate:32 //mp3格式，指定采样率hz、比特率kbps，其他参数使用默认配置；注意：是数字的参数必须提供数字，不要用字符串；需要使用的type类型，需提前把格式支持文件加载进来，比如使用wav格式需要提前加载wav.js编码引擎
            ,onProcess:(buffers:any,powerLevel:any,bufferDuration:any,bufferSampleRate:any,newBufferIdx:any,asyncEnd:any)=>{
                // console.log(bufferDuration)
                if(bufferDuration > 300000) { // 最多录五分钟
                    this.onDgAccept();
                    return;
                }
                this.recordTime = bufferDuration;
                this.setTimeString(bufferDuration);
                //录音实时回调，大约1秒调用12次本回调
                //可利用extensions/waveview.js扩展实时绘制波形
                //可利用extensions/sonic.js扩展实时变速变调，此扩展计算量巨大，onProcess需要返回true开启异步模式
                this.wave.input(buffers[buffers.length-1],powerLevel,bufferSampleRate);//输入音频数据，更新显示波形
            }
        });
    
        //var dialog=createDelayDialog(); 我们可以选择性的弹一个对话框：为了防止移动端浏览器存在第三种情况：用户忽略，并且（或者国产系统UC系）浏览器没有任何回调，此处demo省略了弹窗的代码
        this.rec.open(()=>{//打开麦克风授权获得相关资源
            //dialog&&dialog.Cancel(); 如果开启了弹框，此处需要取消
            //rec.start() 此处可以立即开始录音，但不建议这样编写，因为open是一个延迟漫长的操作，通过两次用户操作来分别调用open和start是推荐的最佳流程
            //success&&success();
            this.setState({
                canRecord: true
            },()=>{
                setTimeout(()=>{
                    if(this.wave == null) {
                        this.wave = Recorder.WaveView({elem:".c-rec-wave"}); //创建wave对象，写这里面浏览器妥妥的
                    }
                    this.wave.input(new Int16Array(1), 0, 1);
                    this.onRecordBtnClick();
                }, 1000)
            })

            
        },(msg:any,isUserNotAllow:any)=>{//用户拒绝未授权或不支持
            //dialog&&dialog.Cancel(); 如果开启了弹框，此处需要取消
            // console.log((isUserNotAllow?"UserNotAllow，":"")+"无法录音:"+msg);
            let e = (isUserNotAllow ? "用户拒绝授权或不支持，" : "") + "无法录音:" + msg;
            Modal.alert("提示", e);
            //fail(e)
        });
    };    
    
    setTimeString = (ms:number)=> {
        let t = util.second2Time(ms, 2, true);
        this.setState({
            time: t.join(":")
        })
    }
    resetTime = ()=> {
        this.setState({
            time: '00:00'
        })
        this.recordTime = 0;
    }
    onRecordBtnClick = ()=> {
        if(this.state.recording) {
            this.rec.pause();
            this.setState({
                recording: false
            })
        }
        else {
            if(!this.started) {
                this.rec.start();
            }
            else {
                this.rec.resume();
            }
            this.started = true;
            this.setState({
                recording: true
            })
        }
    }
    onDgCancel = ()=>{
        this.reset();
        this.rec.stop(()=>{
            this.rec.close();
            this.rs(null);     
        });   
    }
    onDgAccept = ()=> {
        if(!this.started || this.recordTime < 2000) {
            Modal.alert('提示', '没有录音或者录音时间太短');
            return;
        }
        this.reset();
        this.rec.stop((blob:any,duration:any)=>{
            // console.log(blob,(window.URL||webkitURL).createObjectURL(blob),"时长:"+duration+"ms");
            this.rec.close();//释放录音资源，当然可以不释放，后面可以连续调用start；但不释放时系统或浏览器会一直提示在录音，最佳操作是录完就close掉
            //简单利用URL生成播放地址，注意不用了时需要revokeObjectURL，否则霸占内存
            let url = (window.URL||webkitURL).createObjectURL(blob);
            // console.log(this.rs)
            this.rs(url);
        },(msg:any)=>{
            this.rec.close();//可以通过stop方法的第3个参数来自动调用close
        });        
    }
    reset = ()=> {
        this.closeDialog();
        this.resetTime();
        this.started = false;
        this.setState({
            recording: false
        })        
    }
    showDialog() {
        this.recOpen();
        this.setState({
            dialogShow: true
        })
        return new Promise((rs, rj)=>{
            this.rs = rs;
        });
    }
    closeDialog() {
        this.setState({
            dialogShow: false
        })        
    }
    render() {
        let recIcon: ReactNode;
        if(!this.state.recording){
            recIcon = (<i className="c-recbtn-icon c-recbtn-recording" ></i>);
        }
        else {
            recIcon =  (<i className="c-recbtn-icon c-recbtn-pause" ></i>);
        }

      return (
        <Dialog title="录音" onDialogCancel={this.onDgCancel} onDialogAccept={this.onDgAccept} visible={this.state.dialogShow}>
            <div className='c-rec-wave'>

            </div>
            <div className='c-rec-panel'>
                <div className='c-recbtn' style={{display: this.state.canRecord?'flex':'none'}} onClick={this.onRecordBtnClick}>
                    {recIcon}
                </div>
                <div className='c-no-rec' style={{display: !this.state.canRecord?'block':'none'}}>请根据提示授权录音权限,如长时间没有反应,说明浏览器不支持录音,可以尝试选择文件中的录音按钮</div>
                <div className="c-rectime" style={{display: this.state.canRecord?'block':'none'}}>{this.state.time}</div>
            </div>
        </Dialog>
      );
    }    
  }
  
  export default RecordWin;
