class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          {/* <Link to="/" className="c-test">Home</Link>
          <Link to="/About">About</Link>
          <Link to="/Product">Product</Link>
          <Link to="/Comp">Comp</Link> */}
          <Route path="/" exact  render={(props:any) => <Comp {...props} store={store}></Comp>}></Route>
          <Route path="/setting" render={(props:any) => <Setting {...props} store={store}></Setting>}></Route>
          <Redirect path="/comp" to="/" />
        </div>
      </Router>
    );
  }
}

export default App;
