import React from 'react';
import ReactDOM from 'react-dom';
import './InputWin.less';
import Dialog from './Dialog';
const InputWin = (content?:string) => {
    content = content || '';
    let onDgCancel = ()=>{
        reset();  
    }
    let onDgAccept = ()=> {
        let ele = div.querySelector('textarea')||{value:''};
        rs(ele.value);
        reset();       
        
    }
    // 尝试input的时候改变值 无效...
    // let onInutChange = function(e:any) {
    //    // let ele = div.querySelector('textarea');
    //    // ele && (ele.value = e.target.value);
    //     txt = e.target.value;
    //     console.log(txt)
    // }
    let reset = ()=> {
        //这行是网上抄的 试了一下去掉这局 好像也没事...
        ReactDOM.render(React.cloneElement(component, {visible: false}), div)
        ReactDOM.unmountComponentAtNode(div)
        div.remove()
    }    
    const component = <Dialog title="请输入内容" onDialogCancel={onDgCancel} onDialogAccept={onDgAccept} visible={true}>
                        <div>
                          <textarea className='c-inputwin-text'></textarea>
                        </div>
                    </Dialog>
               
    const div = document.createElement('div')
    document.body.append(div)
    ReactDOM.render(component, div);
    let ele = div.querySelector('textarea');
    ele && (ele.value = content);
    let rs:any;
    return new Promise((resolve, rj)=>{
        rs = resolve;
    })
  }
  
export {InputWin}
