import {observable, action, computed} from 'mobx';
export interface AudioObject {
    id: string,
    url: string,
    name: string,
    text: string
}
class Store {
    @observable hideTitle:boolean = false;

    @action setTitleHidden(b:boolean) {  //用来修改todos的action
       this.hideTitle = b;
       localStorage.audio_comp_hide_title = b ? 1 : 0;
       console.log(localStorage.audio_comp_hide_title)
    }
    
    @observable list:Array<AudioObject> = [];

    @action delItem(id: string){
        for(let i = 0; i < this.list.length; i++) {
            if(this.list[i].id == id) {
                let url = this.list[i].url;
                this.list.splice(i, 1);
                if(url.startsWith("blob:")){
                    window.URL.revokeObjectURL(url);
                }
                return;
            }
        }
    }

    @action addItem(item: AudioObject) {
        this.list.push(item);
    }

    @action editItem(item: AudioObject) {
        for(let i = 0; i < this.list.length; i++) {
            if(this.list[i].id == item.id) {
                this.list.splice(i, 1, item);
                return;
            }
        }      
    }

    constructor(){
        this.init();
    }

    releasUrl(){
        this.list.forEach(item=>{
            if(item.url.startsWith("blob:")){
                window.URL.revokeObjectURL(item.url);
            }
        })
    }

    init() {
        this.hideTitle = localStorage.audio_comp_hide_title == "1" ? true : false;
        window.addEventListener("unload", ()=>{this.releasUrl()}, false)
    }

}

export default new Store();
