import React, { Component } from 'react';
import './Dialog.less';
import {Button} from 'antd-mobile';
export interface IProps { 
    visible: boolean;
    title: string;
    onDialogCancel():void;
    //onDialogCancel:()=>void;
    onDialogAccept():void;

}
// 定义 State 类型
export interface IState{
    
}

class Dialog extends Component<IProps, IState> {
    static defaultProps: IProps = {
        visible: false,
        title: '',
        onDialogCancel(){},
        onDialogAccept(){}
    }
    readonly state: IState = {
        
    };
    onCancel = ()=>{
        this.props.onDialogCancel();
    }
    onAccept = ()=> {
        this.props.onDialogAccept();
    }
    render() {
      return (
        <div className='c-dg-mask' style={{display: this.props.visible ? 'block' : 'none'}}>
            <div className='c-dg-box'>
                <div className='c-dg-title'>{this.props.title}</div>
                <div className='c-dg-content'>{this.props.children}</div>
                <div className='c-dg-footer'>
                <Button size="small" onClick={this.onCancel}>取消</Button>
                <Button size='small' onClick={this.onAccept}>确定</Button>
                </div>
            </div>
        </div>
      );
    }    
  }
  
  export default Dialog;
