import React, { Component } from 'react';
import {NavBar, List, Switch, Icon} from 'antd-mobile';
import {observer} from 'mobx-react';
import './Setting.less';
const {default: styled} = require('styled-components');
console.log(styled)
export interface IProps { 
    history:any,
    store: any
}
@observer
class Setting extends Component<IProps> {
   
    render() {
      return (
        <div className="c-page-con">
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => {this.props.history.goBack()}}
            >Setting</NavBar>            
            <List renderHeader={() => '设置'} >
                <List.Item
                extra={<Switch
                    checked={this.props.store.hideTitle}
                    onChange={() => {
                        this.props.store.setTitleHidden(!this.props.store.hideTitle)
                    }}
                />}
                >隐藏标题文字</List.Item>
            </List>
            <List renderHeader={() => '关于'} >
                <List.Item
                extra={<span>小欣</span>}
                >作者</List.Item>
                <List.Item
                extra={<span>41156358</span>}
                >微信</List.Item>                
            </List>        
            <Dist>
                <div className="center"></div>
            </Dist>                    
        </div>
      );
    }
    componentDidMount(){
        // console.log(this.props)
    }
  }
  
export default Setting;

// 这个纯粹是为了测试一下styled-component 没有实际意义
export const Dist = styled.div`
      width: 120px;
      height: 120px;
      background-color: #1E7FF8;
      border-radius: 50%;
      margin: 30px auto;
      position: relative;
      box-shadow: 2px 2px 4px 2px #8e8c8c;
      .center {
        width: 40px;
        height: 40px;
        background: #FFFFFF;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      >div{  // 教程前面还加了一个&表示父元素 ,不加好像也行
          display:none;
      }
      &{  // &表示父元素
          display:none;
      }
`;
