import React, { Component } from 'react';
import {NavBar, ActionSheet, Modal, Toast, WhiteSpace} from 'antd-mobile';
import RecordWin from '../components/RecordWin';
// import {InputWin} from '../components/InputWin'
import './Comp.less'
import '../common/fonts/iconfont.css'
import Player from '../components/Player'
import util from '../common/ts/util';
import {observer} from 'mobx-react';
import { AudioObject } from '../store/store';

export interface IProps { 
  history:any,
  store: any
}

// 定义 State 类型
export interface IState {
    subTitle: string;
    dialogShow: boolean;
    disabled: boolean
}
@observer
class Comp extends Component<IProps, IState>  {
    readonly state: IState = {
        subTitle: '请输入标题',
        dialogShow: false,
        disabled: false
    };
    // public child: any;
    
    // onRef = (ref:any)=> {
    //     this.child = ref;
    // }
    refs: any = React.createRef();
    private subTileChanged = false;
    setDisabled = ()=> {
        this.setState({
            disabled: !this.state.disabled
        })
    }
    render() {
      return (
        <div className="c-page-con">
            <NavBar
                mode="light"
                leftContent={<span className="icon iconfont icon-shezhi c-nav-icon" onClick={()=>{this.onSetting();}}></span>}
                rightContent={<span className="icon iconfont icon-tianjia c-nav-icon" onClick={()=>{this.onAdd();}}></span>}
            >Audio Compare</NavBar>
           
             {this.props.store.hideTitle ?<WhiteSpace size="xl" /> : <div className="c-main-subtitlebox" onClick={this.onChangeSubTitle}>
                <span>{this.state.subTitle ||  "🎵🎵🎵"}</span>
		            <i></i>                
             </div>}
            <input type="file" ref="auFile" accept="audio/*" onChange={this.onFileSelect} style={{display:'none'}}></input>
            
            <div>
              {this.props.store.list.map((item:AudioObject, index:number) => {
                  return <Player key={item.id} store={this.props.store} ref={"player"+index} item={item} onRemove={this.onItemRemove} onPlay={()=>{this.onItemPlay(index)}}></Player>
              })}
            </div>
            
            {/* <div><button onClick={this.test1}>test</button></div> */}
            <RecordWin ref="rwin"></RecordWin>
            {/* <Dialog title="test" onDialogCancel={this.onDgCancel} onDialogAccept={this.onDgAccept} visible={this.state.dialogShow}>sssss</Dialog> */}
        </div>
      );
    }
    componentDidMount(){
      // console.log(this.props); 
    }

    generateId():string {
      let n:number = Math.random() * 10000;
      return n.toString();
    }

    onSetting() {
      this.props.history.push('/Setting');
    }
    test1() {
      // this.props.history.push('/About');
    }
    // onDgCancel = ()=>{
    //     this.setState({
    //       dialogShow: false
    //     })
    // }
    // onDgAccept = ()=> {
    //     this.setState({
    //       dialogShow: false
    //     })
    // }
    onAdd() {
        const BUTTONS = ['选择文件', '输入网址', '录音', '取消'];
        ActionSheet.showActionSheetWithOptions({
          options: BUTTONS,
          cancelButtonIndex: BUTTONS.length - 1,
          //destructiveButtonIndex: BUTTONS.length - 2,
          //title: '请选择',
          message: '请选择添加方式',
          maskClosable: true,
        },
        (buttonIndex) => {
          console.log(buttonIndex)
          switch(buttonIndex) {
              case 0: 
                this.setFile()
                break;
              case 1:
                this.setUrl();
                break  
              case 2:
                this.setRecorder() 
                break;
              default: 
          }

        });        
        // InputWin().then(r=>{
        //     console.log(r)
        // })
        // this.child.showDialog().then((m:any)=>{
        //     console.log("src-> ", m);
        //     this.setState({
        //         url: m
        //     })
        // });
    }    
    onChangeSubTitle = ()=> {
        let txt = this.subTileChanged ? this.state.subTitle : ""
        Modal.prompt('请输入内容', '',
        [
          {
            text: '取消',
            onPress: value => new Promise((resolve) => {
              resolve();
            }),
          },
          {
            text: '确定',
            onPress: value => new Promise((resolve, reject) => {
                value = value.trim();
                this.setState({
                  subTitle: value
                })
                this.subTileChanged = true;
                resolve();
            }),
          },
        ], 'default', txt, ['请输入内容'])         
    }
    setFile() {
        this.refs.auFile.click();
    }
    onFileSelect = (e:any)=>{
        let f = e.target.files[0];
        let typeName = util.getTypeName(f.name);
        if(!["mp3", "wav", "ogg", "webm", "aac", "m4a"].includes(typeName.toLowerCase())) {
            Toast.info("不支持的文件格式");
            return;
        }
        // console.dir(e.target)
        let objectURL = URL.createObjectURL(f);
        // console.log(objectURL)

        let id:string = this.generateId();
        let o:AudioObject = {
          id, url:objectURL, name: util.removeTypeName(f.name), text:""
        }
        this.addAudioObject(o);
        e.target.value = "";
    }
    setUrl() {
        Modal.prompt('请输入网址', '',
        [
          {
            text: '取消',
            onPress: value => new Promise((resolve) => {
              resolve();
            }),
          },
          {
            text: '确定',
            onPress: value => new Promise((resolve, reject) => {
                value = value.trim();
                if(value == "" || !util.isUrl(value)) {
                    Toast.info("请输入正确的网址");
                    reject();
                }
                let id:string = this.generateId();
                let o:AudioObject = {
                  id, url:value, name: "", text:""
                }
                this.addAudioObject(o);
                resolve();
            }),
          },
        ], 'default', '', ['请输入网址']) 
    }

    addAudioObject(o:AudioObject) {
      let list = this.props.store.list;
      if(o.name == ""){
        o.name = "音频" + (list.length + 1);
      }
      this.props.store.addItem(o);
    }

    setRecorder() {
      this.refs.rwin.showDialog().then((url:any)=>{
        if(url == null) {
          return;
        }
        let id:string = this.generateId();
        let o:AudioObject = {
          id, url, name: "",text:""
        }
        this.addAudioObject(o);        
      })
    }
    onItemRemove=(id:string)=> {
      Modal.alert('提示', '确认删除?', [
        { text: '取消', onPress: () => console.log('cancel') },
        { text: '确定', onPress: () => {
          this.props.store.delItem(id);
        }},
      ])      

    }
    onItemPlay = (idx: number)=> {
      for(var i = 0; i < this.props.store.list.length;i++) {
        if(i == idx) {
          continue;
        }
        this.refs["player" + i].onBtnPauseClick();
      }
    }
  }
  
  export default Comp;
