export default  {
    second2Time ( mss:number, level:number, isfixNum:boolean ) {
        function cvtNum(n:number){
          return n >> 0;
        }
        function fixNum(num:number){
          if(num < 10)
            return "0" + num;
          return num + "";  
        }
        let days = cvtNum(mss / (1000 * 60 * 60 * 24));
        let hours = cvtNum((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        let minutes = cvtNum((mss % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = cvtNum((mss % (1000 * 60)) / 1000);
        let arr = [seconds, minutes, hours, days];
        if(level){
          let list = [1, 60, 60, 24];
          for(let i = arr.length - 1; i >= level; i--){
            arr[i-1] += arr[i] * list[i];
          }
          arr = arr.slice(0, level);
        }
        let list = new Array<String>();
        if(isfixNum){
          for(let i = 0; i < arr.length; i++){
            list[i] = fixNum(arr[i]);
          }      
        }
        return list.reverse();
    },
    isIPhone(): boolean {
      return new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
    }, 
    isUrl(url: string): boolean {
      return /^((https|http|ftp|rtsp|mms)?:\/\/)/.test(url);
    },
    getTypeName(fileName: string): string {
      let str = fileName.match(/\.([^\.]*)$/);
      return str ? str[1] : "";
    },
    removeTypeName: function(fileName:string)
    {
      return fileName.replace(/\.[^\.]*$/,"");
    },
}
